<template>
  <div class="basicInfo">
    <div class="Btitle">
      <span class="basicData">开通续费</span>
    </div>
    <div class="contact1">
      <div class="subTitle">当前等级</div>
      <ul style="padding:1.5vh 0 1.5vh 1.3vw;">
        <li>
          <span class="listTitle">版本名称:</span>
          <span class="marginLeft-1">{{ contactTextArr.atName }}</span>
          <span
            class="listTitle expirationTime"
            v-if="contactTextArr.expirationTime"
            >到期时间：{{ contactTextArr.expirationTime }}</span
          >
        </li>
      </ul>
    </div>
    <div class="contact1">
      <div class="subTitle">开通升级</div>
      <ul>
        <!-- 版本名称 -->
        <li style="display:flex;margin-bottom: 1.9vh;margin-top:2.6vh;">
          <span class="listTitle">版本名称:</span>
          <div class="listWrapper">
            <div
              class="list"
              v-for="(i, index) in contactTextArrName"
              :key="index"
              @click="changeTab(index)"
              :class="{ active: index === idx }"
            >
              <img
                class="suggest"
                v-if="index === 1"
                src="@/assets/UserCenter/imgs/suggest.png"
              />
              <img
                class="listBg"
                src="@/assets/UserCenter/imgs/logoRight.png"
              />
              <span class="title">{{ i.name }}</span>
              <div class="listCenter">
                <div>
                  <span
                    class="rmb"
                    :style="{
                      color:
                        index === 1 ? 'rgb(255, 90, 0)' : 'rgb(0, 245, 255)'
                    }"
                    >￥</span
                  >
                  <span
                    class="salePrice"
                    :style="{
                      color:
                        index === 1 ? 'rgb(255, 90, 0)' : 'rgb(0, 245, 255)'
                    }"
                    >{{ i.price }}</span
                  >
                  <span class="unit"
                    >/{{
                      purchaseInfo.title.substr(
                        purchaseInfo.title.length - 1,
                        1
                      )
                    }}</span
                  >
                </div>
                <span class="originPrice">{{ i.raw_price }}</span>
              </div>
            </div>
          </div>
        </li>
        <!-- 购买时长 -->
        <li style="margin-bottom: 1.9vh;">
          <span class="listTitle">购买时长:</span>
          <span v-for="(item, i) in contactTextArr.title" :key="i">
            <span
              :class="{
                saveBtn2: PurchaseTimeIndex == i,
                saveBtn1: PurchaseTimeIndex != i,
                mainBtn: true
              }"
              @click="PurchaseTimeClick(i)"
              >{{ item.title }}</span
            >
          </span>
        </li>
        <!-- 版本详情 -->
        <li style="display:flex;">
          <span class="listTitle">版本详情:</span>
          <table
            class="details"
            style="border-spacing: 0;border-collapse: collapse;"
          >
            <tr style="background-color: rgba(0, 185, 255, 0.05);">
              <th style="width:6.9vw;">一级功能</th>
              <th style="width:12.3vw">二级功能</th>
              <th style="width:7.8vw;">功能内容</th>
            </tr>
            <tr v-for="(i, index) in tableData" :key="index">
              <td>{{ i.name }}</td>
              <td>{{ i.secondDetail }}</td>
              <td>{{ i.detailContent }}</td>
            </tr>
          </table>
        </li>
        <!-- 确认支付 -->
        <span class="saveBtn3 mainBtn" style @click="handleDialog">
          <a>确认支付</a>
        </span>
      </ul>
    </div>
    <Wechat
      :WechatData="WechatData"
      v-if="WechatBl"
      :Wechatcode="Wechatcode"
      class="morePrompt"
    />
    <BalancePay
      :BalancePayData="BalancePayData"
      v-if="BalancePayBl"
      class="morePrompt"
    />
    <RenewDialog v-if="dialogShow" :dialogData="dialogData"></RenewDialog>
  </div>
</template>

<script>
import BalancePay from '@/pages/UserCenter/UserItem/BalancePay.vue';
import Wechat from '@/pages/UserCenter/UserItem/Wechat.vue';
import RenewDialog from './payDialog.vue';
export default {
	components: { Wechat, BalancePay, RenewDialog },
	name: 'renew',
	data() {
		return {
			Wechatcode: '',
			WechatBl: false,
			WechatData: {
				title: '密码',
				name: ['账号', '密码'],
				btn: [
					{
						text: '确认',
						callback(data) {}
					},
					{
						text: '取消',
						callback(data) {}
					}
				]
			},
			BalancePayBl: false,
			BalancePayData: {
				title: '购买等级',
				name: '支付密码：',
				btn: [
					{
						text: '确认',
						callback(data) {}
					},
					{
						text: '取消',
						callback(data) {}
					}
				]
			},
			// 初始化的购买等级
			contactTextArr: [],
			contactTextArrName: [],
      sendersValue: '',
			// 初始化购买等级的按钮样式
			contactBleanIndex: 0,
			// 购买等级
			PurchaseContact: '',
			// 选中等级费用
			monthly_amount: 0,
			defaultPurchaseInfo: {
				name: '专业版',
				title: '一个月'
			},
			// 控制购买时长的样式
			PurchaseTimeIndex: 0,
			// 购买时长
			PurchaseTime: 1,
			timePostBoolean: true,
			// 支付方式的样式
			payBlIndex: 0,
			price: 0,
			// 差值天数
			idx: 1,
			dialogShow: false,
			dialogData: {},
			tableData: []
		};
	},
	methods: {
		init() {
			this.$get('api/buyMessage').then(res => {
				if (res.code === '000000') {
					this.contactTextArr = res.data;

				}
			});
		},
		// 购买时长的点击事件,传入值:index--下标,判断下标改变样式和购买时长（1为1个月，2为2个月...index+1）
		PurchaseTimeClick(index) {
			if (this.timePostBoolean) {
				this.PurchaseTime = index + 1;
				this.PurchaseTimeIndex = index;
				this.contactBleanFn(
					this.contactBleanIndex,
					this.monthly_amount,
					this.PurchaseContact,
					this.timePostBoolean
				);
			}
		},
		close(res) {
			this.dialogShow = res;
		},
		// 购买等级的点击事件,传入值:index--下标,判断下标改变样式和购买等级
		contactBleanFn(i, monthly_amount, grade_name, bl) {
			this.contactBleanIndex = i;
			let vm = this;
			if (bl) {
				bl = false;
			}
		},
		WechatCodeInit() {
			let vm = this;
			vm.$post('api/pay', {
				wayType: 'wx',
				orderType: 2,
				...vm.purchaseInfo,
				money: vm.contactTextArrName[this.idx].price
			}).then(res => {
				if (res.code == '000000') {
					vm.WechatBl = true;
					vm.Wechatcode = res.data;
				}
			});
		},
		changeTab(index) {
			this.idx = index;
			this.tableData = JSON.parse(this.contactTextArrName[index].details);
		},
		handleDialog() {
			let expirationTime;
			let time = this.purchaseInfo.title;
			let now = new Date();
			if (time.indexOf('月') !== -1) {
				expirationTime = now.setMonth(now.getMonth() + 1);
			} else if (time.indexOf('年') !== -1) {
				expirationTime = now.setFullYear(now.getFullYear() + 1);
			}
			this.dialogData = {
				name: this.contactTextArrName[this.idx].name,
				price: this.contactTextArrName[this.idx].price,
				sum_price: this.contactTextArrName[this.idx].sum_price,
				time: this.purchaseInfo.title,
				sendersValue: this.sendersValue,
				expirationTime: now.toLocaleString()
			};
			this.dialogShow = true;
		},
		affirmPay(payType) {
			let vm = this;
			if (payType == 1) {
				this.WechatCodeInit();
				vm.WechatBl = true;
				vm.WechatData.title = '微信支付';
				vm.WechatData.imgText = '请扫描下方二维码进行支付';
				vm.WechatData.name = ['1'];
				vm.WechatData.btn[0].callback = function() {
					vm.WechatBl = false;
				};
			} else if (payType == 3) {
				vm.BalancePayBl = true;
				vm.BalancePayData.btn[0].callback = function(Password) {
					vm.$post('api/pay', {
						wayType: 'account',
						orderType: 2,
						...vm.purchaseInfo,
						money: vm.contactTextArrName[this.idx].price,
						paymentCode: Password
					}).then(res => {
						if (res.data.code == '000000') {
							vm.tips.show(['支付成功', true]);
							vm.BalancePayBl = false;
						} else {
							vm.tips.show([res.err_msg.split(':')[1], false]);
						}
					});
				};
				vm.BalancePayData.btn[1].callback = function() {
					vm.BalancePayBl = false;
				};
			} else if (payType === 2) {
				this.$post('api/pay', {
					wayType: 'ali',
					orderType: 2,
					...this.purchaseInfo,
					money: this.contactTextArrName[this.idx].price
				}).then(res => {
					// 新页面打开
					//   let newPage;
					// newPage = window.open();
					// newPage.document.write(
					// 	'<html><head><title></title><meta charset=\'utf-8\'><body>' +
					//     res +
					//     '</body></html>'
					// );
					// newPage.document.forms[0].submit();
					// 本页面打开
					const div = document.createElement('div');
					div.innerHTML = res;
					document.body.appendChild(div);
					document.forms[0].submit();
				});
			} else {
				vm.tips.show(['请输入正确的充值信息']);
			}
		}
	},
	computed: {
		purchaseInfo() {
			if (this.contactTextArr.name) {
				let name = this.contactTextArr.name[this.contactBleanIndex].name;
				let title = this.contactTextArr.title[this.PurchaseTimeIndex].title;
				let nameSn = this.contactTextArr.name[this.contactBleanIndex].table_sn;
				let titleSn = this.contactTextArr.title[this.PurchaseTimeIndex]
					.table_sn;
				return {
					name,
					title,
					nameSn,
					titleSn
				};
			} else {
				return this.defaultPurchaseInfo;
			}
		}
	},
	created() {
		// 获取用户数据
		this.init();
	},
	watch: {
		purchaseInfo: {
			handler(val) {
				const { title } = val;
				this.$post('api/buyPrice', { title }).then(res => {
					this.contactTextArrName = res.data.price.reverse();
					this.sendersValue = res.data.sendersValue;
					this.tableData = JSON.parse(
						this.contactTextArrName[this.idx].details
					);
				});
			}
		}
	}
};
</script>

<style scoped>
.icon-doubt {
  cursor: pointer;
  font-size: 0.7vw;
  margin-left: 0.4vw;
  color: #c54040;
  position: relative;
  top: -0.2vh;
}
.expirationTime {
  margin-left: 12vw;
}
