  top: 3.2vh;
}
.basicInfo .Btitle {
  height: 3.5vh;
  padding-top: 0.05vh;
}
.basicInfo .Btitle .basicData {
  border-left: solid #28b3fc 0.0625vw;
  padding-left: 0.5vw;
  font-size: 0.9vw;
  margin-right: 3vw;
}
.basicInfo .subTitle {
  font-size: 0.9vw;
  padding: 1.3vh 0 1.3vh 1.3vw;
  border-bottom: solid 0.0625vw #0e121a;
}
