  background-color: #1c2433;
  width: 83.8vw;
  margin-top: 1.5vh;
}
.basicInfo .basic > ul,
.basicInfo .contact1 > ul {
  font-size: 0.8vw;
  padding: 1vh 0 0 1.3vw;
