  display: flex;
  flex-direction: column;
}
.title {
  font-size: 0.7vw;
  margin-top: 1.5vh;
}
.rmb {
  font-size: 0.5vw;
  color: rgb(0, 245, 255);
}
.unit {
  font-size: 0.5vw;
  color: rgba(255, 255, 255, 0.6);
}
.originPrice {
  font-size: 0.5vw;
  color: rgba(255, 255, 255, 0.6);
  text-decoration: line-through;
  margin-left: 0.8vw;
  margin-top: -0.4vh;
}
.salePrice {
  font-size: 1vw;
  color: rgb(0, 245, 255);
}
.saveBtn2 {
  background-color: #1d4c6c;
  border: solid 0.0625vw #00b9ff;
}
.saveBtn1 {
  background-color: #121721;
  border: solid 0.0625vw #475566;
}
.mainBtn {
  margin-left: 1vw;
}
.basicInfo .Btitle .saveBtn1 {
  width: 5.2vw;
  height: 1.6vw;
  background-color: #283247;
}
.saveBtn1 a,
.saveBtn2 a {
  width: 3.4vw;
  height: 0.8vw;
  font-family: SourceHanSansCN-Regular;
  font-size: 0.8vw;
  font-weight: normal;
  font-stretch: normal;
  color: #ffffff;
}
.mainBtn:hover {
  border: 0.0625vw solid #00b9ff;
}
.marginLeft-1 {
  color: #00b9ff;
