  font-size: 0.8vw;
  background-color: #283247;
  border: solid 0.0625vw #283247;
  height: 3.5vh;
  width: 5vw;
  line-height: 2.9vh;
  text-align: center;
  display: inline-block;
  margin-top: 0.6vh;
  margin-bottom: 2.8vh;
}
.saveBtn3:hover {
  background-color: #1d4c6c;
  border: solid 0.0625vw #00b9ff;
}
.mainBtn {
  padding: 0.3vh 0.6vw;
}
.listTitleImg1 {
  width: 0.8vw;
  height: 0.8vw;
  position: relative;
  top: 0.2vh;
}
.details th {
  font-size: 0.7vw;
  text-align: center;
  border: 1px solid rgba(31, 38, 51, 1);
  height: 4.9vh;
}
.details td {
  font-size: 0.6vw;
  font-family: SourceHanSansCN-Regular;
