<template>
  <div class="dialog">
    <div class="content">
      <div class="title">
        <span>全天智能{{dialogData.name}}-支付开通</span>
        <i @click="close" class="biFont icon-delete2"></i>
      </div>
      <div class="details">
        <div class="list">
          <label>开通账户:</label>
          <span>24e@24e.co</span>
        </div>
        <div class="list">
          <label>开通版本:</label>
          <span>{{dialogData.name}}</span>
        </div>
        <div class="list">
          <label>版本价格:</label>
          <span>￥{{dialogData.price}}</span>
        </div>
        <div class="list">
          <label>购买时长:</label>
          <span>{{dialogData.time}}</span>
        </div>
        <div class="list">
          <label>到期时间:</label>
          <span>{{dialogData.expirationTime}}</span>
        </div>
        <div class="total">
          <span>合计:</span>
          <div class="totalPrice">
            <p>
              订单金额:
              <span>￥ {{dialogData.price}}</span>
            </p>
            <div class="hoverWrapper">
              应付合计:
              <span class="shouldPay">￥ {{dialogData.sum_price}}</span>
            <i v-if="dialogData.sendersValue" class="biFont icon-doubt" @mouseover="hoverShow = true" @mouseleave="hoverShow = false"/>
            <div v-if="hoverShow" class="hoverSpan">{{dialogData.sendersValue}}</div>
            </div>
          </div>
        </div>
        <p class="tip">
          如有其他疑问,请邮箱至
          <span>24e@24e.co</span>
        </p>
        <div class="confirmBtn">
          <span
            :class="{saveBtn2:payBlIndex==i,saveBtn1:payBlIndex!=i,mainBtn:true}"
            v-for="(payItem,i) in payArr"
            :key="i"
            @click="payClick(i)"
          >
            <img :src="payItem.img" alt class="listTitleImg1" />
            <a>{{payItem.name}}</a>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["dialogData"],
  data() {
    return {
      payArr: [
        {
          name: "微信支付",
          img: require("@/assets/UserCenter/imgs/weixinzhifu.svg")
        },
        {
          name: "支付宝支付",
          img: require("@/assets/UserCenter/imgs/zhifubao.svg")
        }
      ],
      payBlIndex: -1,
      hoverShow: false
    };
  },
  methods: {
    // 支付方式的点击事件,传入值:index--下标,判断下标改变样式和支付方式（1为微信，2为支付宝，3为余额）
    payClick(i) {
      this.payBlIndex = i;
      let payType = ++i
      this.$parent.affirmPay(payType);
    },
    close() {
      this.$parent.close(false);
    }
  }
};
</script>

<style scoped>
i {
  cursor: pointer;
  margin-left: 10px;
}
.dialog {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  color: #fff;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.content {
  width: 20.3vw;
  height: 44.6vh;
  background-color: #19202e;
  border-radius: 1.1vh;
  box-sizing: border-box;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  padding: 2vh 1vw 2.3vh;
}
.title {
  font-size: 0.8vw;
  display: flex;
  justify-content: space-between;
  padding-bottom: 1.9vh;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.details > .list {
  font-size: 0.6vw;
  margin-top: 1.7vh;
  margin-left: 1vw;
}
.list span {
  margin-left: 0.9vw;
}
.total {
  display: flex;
  justify-content: space-between;
  margin-top: 1.9vh;
  padding: 1.5vh 1.1vw 1.5vh 1.2vw;
  border: solid 1px rgba(255, 255, 255, 0.1);
}
.tip {
  font-size: 0.5vw;
  margin-left: 1vw;
  margin-top: 0.6vh;
  padding-bottom: 1.9vh;
  border-bottom: solid 1px rgba(255, 255, 255, 0.1);
}
.tip span {
  color: rgb(0, 185, 255);
}
.confirmBtn {
  margin-top: 1.9vh;
  text-align: center;
}
.saveBtn2 {
  background-color: #1d4c6c;
  border: solid 0.0625vw #00b9ff;
}
.saveBtn1 {
  width: 5.2vw;
  height: 1.6vw;
  background-color: #283247;
}
.saveBtn1 a,
.saveBtn2 a {
  width: 3.4vw;
  height: 0.8vw;
  font-family: SourceHanSansCN-Regular;
  font-size: 0.6vw;
  font-weight: normal;
  font-stretch: normal;
  color: #ffffff;
}
.mainBtn {
  margin-left: 1vw;
  padding: 0.6vh 0.6vw;
}
.mainBtn:hover {
  border: 0.0625vw solid #00b9ff;
}
.listTitleImg1 {
  width: 0.8vw;
  height: 0.8vw;
  position: relative;
  top: 0.2vh;
  margin-right: 0.3vw;
}
.totalPrice span {
  margin-left: 1.2vw;
}
.shouldPay {
  color: rgb(0, 185, 255);
  font-size: 0.8vw;
}
.hoverWrapper {
  position: relative;
}
.hoverSpan {
  background-color: #19202e;
  position: absolute;
  right: -22px;
  top: -111px;
  color: rgb(0, 185, 255);
}
</style>
